  constructor() {
  }
  formatFeatures(features, parent) {
    let obj = {};
    let childFeatures = features.filter(obj => obj['parent_feature'] === parent);

    if (parent == null) {
      childFeatures.forEach(feature => {
        obj[feature['_id']] = Object.assign({ active: feature['active'] }, this.formatFeatures(features, feature['_id']));
      })
    } else {
      return childFeatures.reduce((acc, obj) => {
        acc[obj['_id']] = obj['active'];
        return acc;
      }, {});
    }
    return obj;
  }
